<template>
  <div class="mdl-cell--4-col-phone text-center">
    <h3>Production</h3>
    <form method="post"
      novalidate>
      <div v-show="!validProductSelected">
        <autocomplete
          :data="products.data"
          :action="fetchProducts"
          :field="'name'"
          :multiSelect="false"
          :placeholder="'Select Product'"
          @selected="selectedProduct=$event"
          v-if="!validProductSelected"
          ></autocomplete>
          <p v-else>{{product.name}}</p>
      </div>
      <div v-show="validProductSelected">
        <div>
          <h5>{{product.name}}</h5>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('number_of_hours')}">
            <input v-validate="'required|integer|min_value:1'" name="number_of_hours" id="number_of_hours" class="mdl-textfield__input" v-model="numberOfHours">
            <label class="mdl-textfield__label" for="number_of_hours">Number of Hours</label>
            <span class="mdl-textfield__error">{{ errors.first('number_of_hours') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('product_quantity')}">
            <input v-validate="'required|integer|min_value:1'" name="product_quantity" id="product_quantity" class="mdl-textfield__input" v-model="productionParams.product_quantity">
            <label class="mdl-textfield__label" for="product_quantity">Product</label>
            <span class="mdl-textfield__error">{{ errors.first('product_quantity') }}</span>
          </div>
        </div>
        <div>
          <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('apprx_raw_material_weight')}">
            <input v-validate="'required|integer|min_value:1'" name="apprx_raw_material_weight" id="apprx_raw_material_weight" class="mdl-textfield__input" v-model="productionParams.apprx_raw_material_weight">
            <label class="mdl-textfield__label" for="apprx_raw_material_weight">Apprx Raw Material Weight</label>
            <span class="mdl-textfield__error">{{ errors.first('apprx_raw_material_weight') }}</span>
          </div>
        </div>
         <button
            class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
            type="button"
            @click="addProduction">Add
          </button>
      </div>
    </form>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { Autocomplete } from 'SHARED/autocomplete'
import { MESSAGES } from '@/constants'

export default {
  name: 'production-create',
  components: {
    Autocomplete
  },
  mounted () {
    window.componentHandler.upgradeAllRegistered()
  },
  computed: {
    ...mapGetters({
      products: 'getProducts'
    }),
    productionParams () {
      return this.params
    },
    selectedProduct: {
      get () {
        return this.product
      },
      set (product) {
        this.product = product
        this.productionParams.product = product.id
        this.validProductSelected = true
        window.componentHandler.upgradeAllRegistered()
      }
    },
    numberOfHours: {
      get () {
        return this.params.number_of_hours / 3600
      },
      set (value) {
        this.params.number_of_hours = value * 3600
      }
    }
  },
  methods: {
    ...mapActions([
      'fetchProducts',
      'createProduction'
    ]),
    addProduction () {
      this.$validator.validateAll().then(valid => {
        if (valid) {
          this.createProduction(this.params).then(() => {
            this.$toastr.s(MESSAGES.success_add)
            this.$emit('added', true)
          }).catch(e => {
            this.$toastr.e(e)
          })
        }
      })
    }
  },
  data () {
    return {
      product: {},
      validProductSelected: false,
      hrs: 0,
      minutes: 0,
      params: {
        product: undefined,
        number_of_hours: '',
        product_quantity: 0,
        apprx_raw_material_weight: 0
      }
    }
  }
}
</script>



